import Link from 'next/link';
import { getDigitalProducts } from '../../lib/notion';
import Header from '../../components/Header';
import Footer from '../../components/Footer';

export const metadata = {
    title: 'Produk Digital - KrisDigital Studio',
    description: 'Katalog template, script, dan produk digital untuk produktivitas Anda.',
};

export default async function ProductsPage() {
    const products = await getDigitalProducts();

    return (
        <div className="min-h-screen bg-slate-50">
            <Header />
            <main className="pt-28 pb-16 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
                <div className="text-center mb-16">
                    <h1 className="text-4xl md:text-5xl font-bold text-slate-900 mb-4">Produk Digital</h1>
                    <p className="text-lg text-slate-600 max-w-2xl mx-auto">
                        Kumpulan aset, template, dan solusi digital siap pakai untuk mempercepat proyek Anda.
                    </p>
                </div>

                {products.length === 0 ? (
                    <div className="bg-white rounded-2xl p-12 text-center shadow-sm border border-slate-100">
                        <h3 className="text-xl font-semibold text-slate-900 mb-2">Belum ada produk</h3>
                        <p className="text-slate-500">Katalog produk saat ini sedang diperbarui.</p>
                    </div>
                ) : (
                    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
                        {products.map((product) => (
                            <Link key={product.id} href={`/products/${product.slug}`} className="block group h-full">
                                <div className="bg-white rounded-2xl shadow-sm border border-slate-100 overflow-hidden transition-all duration-300 hover:shadow-xl hover:-translate-y-1 h-full flex flex-col relative">

                                    {/* Badge Diskon atau Gratis */}
                                    {product.isFreebie && (
                                        <div className="absolute top-4 right-4 z-10 bg-green-500 text-white text-xs font-bold px-3 py-1 rounded-full shadow-md">
                                            GRATIS
                                        </div>
                                    )}
                                    {product.discountPrice > 0 && !product.isFreebie && (
                                        <div className="absolute top-4 right-4 z-10 bg-red-500 text-white text-xs font-bold px-3 py-1 rounded-full shadow-md">
                                            PROMO
                                        </div>
                                    )}

                                    {product.cover ? (
                                        <div className="h-48 overflow-hidden bg-slate-100 relative">
                                            <img src={product.cover} alt={product.title} className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-500" />
                                            {product.productType && (
                                                <div className="absolute top-4 left-4">
                                                    <span className="px-3 py-1 bg-white/90 backdrop-blur-sm text-blue-600 text-xs font-semibold rounded-full shadow-sm">
                                                        {product.productType}
                                                    </span>
                                                </div>
                                            )}
                                        </div>
                                    ) : (
                                        <div className="h-48 bg-gradient-to-br from-indigo-50 to-purple-50 flex items-center justify-center">
                                            <svg className="w-12 h-12 text-indigo-300" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M5 8h14M5 8a2 2 0 110-4h14a2 2 0 110 4M5 8v10a2 2 0 002 2h10a2 2 0 002-2V8m-9 4h4" />
                                            </svg>
                                        </div>
                                    )}

                                    <div className="p-6 flex flex-col flex-grow">
                                        <h2 className="text-xl font-bold text-slate-900 mb-2 group-hover:text-blue-600 transition-colors">
                                            {product.title}
                                        </h2>
                                        <p className="text-slate-600 line-clamp-2 text-sm mb-4 flex-grow">
                                            {product.description}
                                        </p>

                                        <div className="pt-4 border-t border-slate-100 mt-auto flex items-end justify-between">
                                            <div>
                                                {product.isFreebie ? (
                                                    <span className="text-xl font-bold text-green-600">Rp 0</span>
                                                ) : product.discountPrice > 0 ? (
                                                    <div>
                                                        <span className="text-sm text-slate-400 line-through block mb-0.5">
                                                            Rp {product.price.toLocaleString('id-ID')}
                                                        </span>
                                                        <span className="text-xl font-bold text-slate-900">
                                                            Rp {product.discountPrice.toLocaleString('id-ID')}
                                                        </span>
                                                    </div>
                                                ) : (
                                                    <span className="text-xl font-bold text-slate-900">
                                                        Rp {product.price ? product.price.toLocaleString('id-ID') : '0'}
                                                    </span>
                                                )}
                                            </div>

                                            <button className="px-4 py-2 bg-blue-50 text-blue-600 text-sm font-semibold rounded-lg group-hover:bg-blue-600 group-hover:text-white transition-colors">
                                                Lihat Detail
                                            </button>
                                        </div>
                                    </div>
                                </div>
                            </Link>
                        ))}
                    </div>
                )}
            </main>
            <Footer />
        </div>
    );
}
